.ref-title {
  color: var(--heading-color);
  grid-area: 1 / 1 / 3 / 13;
  margin-left: 5rem;
  font-size: 4rem;
}

.ref-box {
  margin: 0 3rem;
  padding: 0 2rem;
}

.ref-box h3 {
  color: var(--subheading-color);
  font-size: 2rem;
}

.ref-data {
  grid-area: 3 / 1 / 9 / 7;
}

.ref-doc {
  grid-area: 9 / 1 / 13 / 7;
}

.ref-code {
  grid-area: 3 / 7 / 9 / 13;
  margin-left: 0;
}

.ref-other {
  grid-area: 9 / 7 / 13 / 13;
  margin-left: 0;
}

.ref-item-container {
  margin: 0;
  padding: 0;
}

.ref-item {
  background-color: #ffffffe6;
  border-radius: 5px;
  align-items: center;
  margin-bottom: 1rem;
  padding: .8rem;
  font-size: 1.2rem;
  transition: transform .3s ease-in-out;
  display: flex;
}

.ref-item-container a {
  color: var(--background-color);
  -webkit-text-decoration: inherit;
  text-decoration: inherit;
  width: 100%;
  height: 100%;
}

.ref-item-container a .ref-item:hover {
  transform: translateX(.5rem);
}

.ref-icon {
  height: 1.5rem;
  aspect-ratio: 1 / 1;
  margin-right: 1rem;
}

.ref-disclaimer {
  color: #ffffffb3;
  margin-top: 1rem;
  font-size: .8rem;
  font-style: italic;
}

.ref-cookie {
  color: #ffffffb3;
  all: unset;
  font-size: .8rem;
  font-style: italic;
  text-decoration: underline;
}

.ref-cookie:hover {
  color: #fff;
  cursor: pointer;
}

